"use client"

import { useState, useEffect } from "react"
import { useRouter } from "next/navigation"
import Link from "next/link"
import Image from "next/image"
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { Badge } from "@/components/ui/badge"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { 
  Plus, Play, Square, Trash2, Edit, Send, 
  Slack, LogOut, Activity, BarChart3, Users, 
  Loader2, Sparkles, MessageSquare, Settings
} from "lucide-react"

interface Promotion {
  id: number;
  title: string;
  points: number;
  limit: number;
  status: "READY" | "ACTIVE" | "FINISHED";
}

export default function AdminDashboard() {
  const router = useRouter()
  const [isAuthorized, setIsAuthorized] = useState(false)
  const [promotions, setPromotions] = useState<Promotion[]>([
    { id: 1, title: "선착순 1000명 포인트 증정", points: 5000, limit: 1000, status: "READY" },
    { id: 2, title: "오픈 기념 웰컴 포인트", points: 3000, limit: 500, status: "ACTIVE" },
  ])

//   useEffect(() => {
//     const role = localStorage.getItem("role")
//     if (role !== "admin") {
//       router.replace("/login")
//     } else {
//       setIsAuthorized(true)
//     }
//   }, [router])

  const toggleStatus = (id: number, status: "ACTIVE" | "FINISHED") => {
    setPromotions(prev => prev.map(p => p.id === id ? { ...p, status } : p))
  }

  const handleDelete = (id: number) => {
    if(confirm("정말 삭제하시겠습니까?")) setPromotions(prev => prev.filter(p => p.id !== id))
  }

  const handleLogout = () => {
    if (confirm("로그아웃 하시겠습니까?")) {
      localStorage.clear()
      router.push("/")
    }
  }

  const sendSlackTest = () => alert("Slack으로 [서비스 상태 테스트] 메시지를 보냈습니다.")
  const sendSlackReport = () => alert("Slack으로 [서버 가동 리포트]를 발송했습니다.")

//   if (!isAuthorized) {
//     return (
//       <div className="min-h-screen flex flex-col items-center justify-center bg-background">
//         <Loader2 className="h-10 w-10 animate-spin text-primary mb-4" />
//         <p className="text-sm text-muted-foreground animate-pulse">관리자 권한 확인 중...</p>
//       </div>
//     )
//   }

  return (
    <div className="min-h-screen bg-background">
      {/* 1. 사용자 대시보드와 동일한 헤더 */}
      <header className="border-b border-border bg-card/50 backdrop-blur-sm sticky top-0 z-50">
        <div className="container mx-auto px-4 h-16 flex items-center justify-between">
          <Link href="/admin" className="flex items-center gap-2">
            <Image src="/logo.png" alt="Next Me" width={120} height={32} className="h-8 w-auto" priority />
            <Badge variant="outline" className="ml-2 border-primary text-primary font-bold">ADMIN</Badge>
          </Link>
          <div className="flex items-center gap-4">
            <Button variant="ghost" size="icon"><MessageSquare className="h-5 w-5" /></Button>
            <Button variant="ghost" size="icon"><Settings className="h-5 w-5" /></Button>
            <Button variant="ghost" size="icon" onClick={handleLogout} title="로그아웃">
              <LogOut className="h-5 w-5 text-muted-foreground hover:text-destructive transition-colors" />
            </Button>
            <Avatar className="border"><AvatarFallback className="bg-primary/5 italic">AD</AvatarFallback></Avatar>
          </div>
        </div>
      </header>

      <div className="container mx-auto px-4 py-8">
        {/* 2. 대시보드 타이틀 영역 */}
        <div className="flex justify-between items-end mb-8">
          <div>
            <h1 className="text-3xl font-bold mb-2 text-foreground">시스템 관리 </h1>
            <p className="text-muted-foreground font-medium">프로모션 상태 및 서버 안정성을 모니터링합니다</p>
          </div>
          <div className="flex gap-2">
            <Button variant="outline" size="sm" className="border-green-600 text-green-600 hover:bg-green-50" onClick={sendSlackTest}>
              <Slack className="h-4 w-4 mr-2" /> 상태 테스트
            </Button>
            <Button size="sm" className="bg-[#4A154B] hover:bg-[#3b113b] text-white" onClick={sendSlackReport}>
              <Send className="h-4 w-4 mr-2" /> 리포트 전송
            </Button>
          </div>
        </div>

        {/* 3. 상단 요약 카드 (사용자 대시보드와 동일 스타일) */}
        <div className="grid md:grid-cols-3 gap-6 mb-8">
          <Card className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-lg bg-green-100 flex items-center justify-center"><Activity className="h-5 w-5 text-green-600" /></div>
              <Badge className="bg-green-500">Normal</Badge>
            </div>
            <p className="text-sm text-muted-foreground mb-1 font-bold">서버 엔진 상태</p>
            <p className="text-2xl font-black">Operational</p>
          </Card>
          <Card className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-lg bg-primary/10 flex items-center justify-center"><BarChart3 className="h-5 w-5 text-primary" /></div>
            </div>
            <p className="text-sm text-muted-foreground mb-1 font-bold">진행중인 프로모션</p>
            <p className="text-2xl font-black">{promotions.filter(p=>p.status==="ACTIVE").length} 건</p>
          </Card>
          <Card className="p-6">
            <div className="flex items-center justify-between mb-4">
              <div className="h-10 w-10 rounded-lg bg-accent/10 flex items-center justify-center"><Users className="h-5 w-5 text-accent" /></div>
            </div>
            <p className="text-sm text-muted-foreground mb-1 font-bold">대기중인 리포트</p>
            <p className="text-2xl font-black">0 건</p>
          </Card>
        </div>

        <div className="grid lg:grid-cols-3 gap-6">
          <div className="lg:col-span-2 space-y-6">
            {/* 4. 프로모션 테이블 카드 */}
            <Card className="p-6 border-primary/20 shadow-sm overflow-hidden">
              <div className="flex items-center justify-between mb-6">
                <h2 className="text-xl font-semibold flex items-center gap-2 font-bold">
                  <Sparkles className="h-5 w-5 text-primary" /> 프로모션 관리
                </h2>
                <Button size="sm" className="h-8">
                  <Plus className="h-4 w-4 mr-1" /> 생성
                </Button>
              </div>

              <div className="overflow-x-auto">
                <table className="w-full text-left">
                  <thead className="text-muted-foreground text-xs font-bold uppercase border-b border-border/50">
                    <tr>
                      <th className="px-2 py-3">이벤트명</th>
                      <th className="px-2 py-3">포인트</th>
                      <th className="px-2 py-3">인원</th>
                      <th className="px-2 py-3">상태</th>
                      <th className="px-2 py-3 text-right">제어</th>
                    </tr>
                  </thead>
                  <tbody className="divide-y divide-border/50 text-sm">
                    {promotions.map((p) => (
                      <tr key={p.id} className="group hover:bg-secondary/20 transition-all">
                        <td className="px-2 py-4 font-bold text-foreground">{p.title}</td>
                        <td className="px-2 py-4 text-blue-600 font-bold">{p.points.toLocaleString()}P</td>
                        <td className="px-2 py-4 font-mono">{p.limit}명</td>
                        <td className="px-2 py-4">
                          {p.status === "READY" && <Badge variant="secondary">준비</Badge>}
                          {p.status === "ACTIVE" && <Badge className="bg-green-500">진행</Badge>}
                          {p.status === "FINISHED" && <Badge variant="destructive">종료</Badge>}
                        </td>
                        <td className="px-2 py-4 text-right flex justify-end gap-1">
                          {p.status === "READY" && (
                            <Button size="icon" variant="ghost" className="h-8 w-8 text-green-600" onClick={() => toggleStatus(p.id, "ACTIVE")}>
                              <Play className="h-4 w-4 fill-current" />
                            </Button>
                          )}
                          {p.status === "ACTIVE" && (
                            <Button size="icon" variant="ghost" className="h-8 w-8 text-red-600" onClick={() => toggleStatus(p.id, "FINISHED")}>
                              <Square className="h-4 w-4 fill-current" />
                            </Button>
                          )}
                          <Button size="icon" variant="ghost" className="h-8 w-8 text-muted-foreground hover:text-primary"><Edit className="h-4 w-4" /></Button>
                          <Button size="icon" variant="ghost" className="h-8 w-8 text-muted-foreground hover:text-destructive" onClick={() => handleDelete(p.id)}><Trash2 className="h-4 w-4" /></Button>
                        </td>
                      </tr>
                    ))}
                  </tbody>
                </table>
              </div>
            </Card>
          </div>

          {/* 5. 우측 퀵 액션 레이아웃 (사용자 대시보드와 동일) */}
          <div className="space-y-6">
            <Card className="p-6">
              <h2 className="text-lg font-semibold mb-4">빠른 시스템 도구</h2>
              <div className="space-y-3">
                <Button variant="outline" className="w-full justify-start bg-transparent py-6 h-auto" onClick={sendSlackTest}>
                  <Slack className="h-5 w-5 mr-3 text-primary" /> 슬랙 상태 전송
                </Button>
                <Button variant="outline" className="w-full justify-start bg-transparent py-6 h-auto" onClick={sendSlackReport}>
                  <BarChart3 className="h-5 w-5 mr-3 text-primary" /> 데이터 리포트 발송
                </Button>
              </div>
            </Card>

            <Card className="p-6 bg-gradient-to-br from-primary/5 to-accent/5 border-primary/10">
              <h2 className="text-lg font-semibold mb-2 flex items-center gap-2"><Sparkles className="h-4 w-4 text-primary"/>관리자 팁</h2>
              <p className="text-sm text-muted-foreground mb-4 leading-relaxed">프로모션을 시작하면 사용자 앱 메인 화면에 즉시 노출됩니다. 시작 전 지급 포인트 설정을 다시 한번 확인하세요.</p>
              <Button variant="link" size="sm" className="p-0 text-primary h-auto font-bold">운영 매뉴얼 보기 →</Button>
            </Card>
          </div>
        </div>
      </div>
    </div>
  )
}